@import "./_api.css" layer(pix);
@import "./_reset.css" layer(pix);
@import "./_typography.css" layer(pix);
@import "./themes/index.css" layer(pix);
@import "./_layout.css" layer(pix);
@import "./components/index.css" layer(pix);
@import "./_preferences.css" layer(pix);
@import "./_a11y.css" layer(pix);

style:not([type]) {
	position: sticky;
	top: 0;

	display: block;

	width: 100%;
	height: 100vh;

	outline: none;

	font-family: var(--pix--font-family-monospace);
	font-size: 0.8em;
	line-height: 1.25;
	font-weight: 400;

	text-align: left;
	white-space: pre;

	padding: var(--pix--spacing);

	overflow: auto;
	overscroll-behavior: none;

	background-color: var(--pix--color);
	color: var(--pix--background-color);
}

style[type="text/css"] {
	display: none;
}

.dropdown.theme {
	width: 10rem;
}

.dropdown.color-scheme {
	width: 12rem;
}
